import styled from 'styled-components'

const WinText = styled.h1`
  position: absolute;
  width: 100%;
  top: 50%;
  text-align: center;
  text-shadow: 0px 0px 30px rgba(0, 0, 0, 1);
`
WinText.displayName = 'WinText'

export { WinText }



